import {
  okregi,
  sondaze,
  sondazeDoSredniej,
  KOLOR_PARTII,
  glosy2019,
  srednia,
  sredniaSondazy,
} from "./dane";
import {
  Wynik,
  Okreg,
  Sondaze,
  ILE_SYMULACJI,
  WynikDemo,
  Histogram,
} from "./typy";
import {
  odmianaSlowaMandat,
  rysujHistogram,
  rysujWynikWyborow,
} from "./wykresy";
import {
  NaDemo,
  NaPolDemo,
  PodzialMandatowKraj,
  PodzialMandatowWOkregu,
  PoparcieWOkregu,
  RozkladPrawdopodobienstwaWOkregu,
} from "./ordynacja";

const sondazePolDemo = {};
Object.entries(sondaze).forEach(([pracownia, wynik]) => {
  sondazePolDemo[pracownia] = NaPolDemo(wynik as Wynik);
});
rysujWynikWyborow( { średnia: NaPolDemo(srednia), ...sondazePolDemo }, "sondaze", { laczOpozycje: false });

const wynikiWgSondazy: Sondaze = {};
wynikiWgSondazy["średnia"] = NaPolDemo(PodzialMandatowKraj(sredniaSondazy));
Object.entries(sondaze).forEach(
  ([sondaz, wynik]) =>
    (wynikiWgSondazy[sondaz] = NaPolDemo(PodzialMandatowKraj(wynik as Wynik)))
);


rysujWynikWyborow(wynikiWgSondazy, "mandatyKraj", {
poziomo: true,
laczOpozycje: false,
calySejm: true,
});

const podzialWOkregach: Sondaze = {};
const podzialWOkregachDemo: Sondaze = {};
okregi.forEach((okreg) => {
const wynikWOkregu = PoparcieWOkregu(sredniaSondazy, okreg);
podzialWOkregach[okreg.miasto] = NaPolDemo(PodzialMandatowWOkregu(
    wynikWOkregu,
    okreg
));
podzialWOkregachDemo[okreg.miasto] = NaDemo(
    PodzialMandatowWOkregu(wynikWOkregu, okreg)
);
});

setTimeout(function () {
rysujWynikWyborow(podzialWOkregach, "mandatyWOkregach", {
    poziomo: true,
    laczOpozycje: false,
});
rysujWynikWyborow(podzialWOkregachDemo, "mandatyWOkregachDemo", {
    poziomo: true,
    laczOpozycje: true,
});
}, 200);